<template lang="html">
	<!--最外层只能有一个-->
	<div class="loginBox">
		<!--<headname></headname>-->
		<div class="iptRow">
			用户名：
			<ipt v-model="userName" type="text" placeholder="请输入账号"></ipt>
		</div>
		<div class="iptRow">
			密&nbsp;&nbsp;&nbsp;码：
			<ipt v-model="passWord" class="bxdIpt" type="password" placeholder="请输入密码"></ipt>
		</div>
		<span @click="loginBtn()" class="bigBtn">登录</span>
		<!--不能这样写-->
		<!--当组件使用驼峰时，这里需要用-隔开驼峰-->
		<!--<CommonFooter></CommonFooter>-->
		<!--<common-footer></common-footer>-->
	</div>
</template>

<script>
//	import HomeHeader from '../components/HomeHeader'
	import ipt from '../components/inputComponent'
//	import CommonFooter from '../components/commonFooter'
	export default {
		data ()  {
			return {
				userName: '',
				passWord: '',
			}
		},
		components: {
//			headname: HomeHeader,
//			CommonFooter: CommonFooter,
			ipt: ipt
		},
		methods: {
			loginBtn () {
				//使用这个来跳转路由
				this.$router.push('/index');
			}
		},
	}
</script>

<style>
	.loginBox{
		padding-top: 20vh;
		box-sizing: border-box;
		height: 100vh;
		text-align: center;
		background: url(../assets/image/yyzt.jpg) no-repeat;
		background-size: 100% 100%;
	}
	.iptRow{
		margin-bottom: 1rem;
		height: 2rem;
		line-height: 2rem;
		font-size: .8rem;
		color: #fff;
	}
	input{
		width: 60%;
		box-sizing: border-box;
		height: 1.4rem;
	}
	.bigBtn{
		margin-top: 1rem;
		display: inline-block;
		border: none;
		width: 80%;
		line-height: 40px;
		background-color: rgba(100, 251, 240, 0.5);
	}
</style>


